<template>
  <v-container fluid pa-0>
    <v-row no-gutters>
      <ToolBar
      :version="version"
      @setMode="setMode"
      >
      </ToolBar>
      <v-col cols="12">
        <v-btn large color="primary" @click="setContainer('add')">Add Container</v-btn>
        <v-btn large color="warning" @click="setContainer('delete')">Delete Container</v-btn>
      </v-col>
      <v-col cols="3" v-for="(container, index) in containerList" v-bind:key="index">
        <Panel 
          :container="container.value"
        ></Panel>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { KunlunApi } from '../api'
import store from '../store'
import Panel from './Panel'
import ToolBar from '../ToolBar'

export default {
  name: 'SetContainernPage',
  components: {
    ToolBar,
    Panel,
  },
  data: () => ({
    containerList: [],
    station: '',
    version: '',
    mode: 'PRODUCT'
  }),
  mounted () {
    this.url = window.location.hash.substring(1)  // "/kunlun/pcbst"
    this.station = this.url.split('/')[2].toUpperCase()
    this.getPlatformVersion()
  },
  beforeDestroy () {
  },
  methods: {
    getContainerList () {
      KunlunApi({ service: 'get-all-containers', station: this.station })
        .then(response => {
          this.containerList = response.data.data
          // store.commit('setMessage', response.data.message)
          // console.log(this.containerList)
        })
        .catch(e => {
          console.log(e)
        })
    },
    getPlatformVersion () {
      KunlunApi({ service: 'get-platform-version' })
        .then(response => {
          this.version = response.data.data
          this.getContainerList()
        })
        .catch(e => {
          console.log(e)
        })
    },
    setContainer (action) {
      KunlunApi({ service: 'set-container', station: this.station, action: action })
        .then(response => {
          // console.log(response.data.message)
          store.commit('setMessage', response.data.message)
          this.getContainerList()
        })
        .catch(e => {
          console.log(e)
        })
    },
    setMode (mode) {
      this.mode = mode
    }
  }
}
</script>
